.album {
  margin-left: auto;
  margin-right: auto;
  padding-left: 0;
  padding-bottom: 0;
  width: 648px;

  list-style: none;
}

.album__item {
  padding-top: 23px;
  padding-bottom: 25px;

  border-top: 2px solid #e5e5e5;
}

@media (min-width: 768px) {
  .album {
    display: flex;
    flex-wrap: wrap;
  }

  .album__item {
    width: 50%;
  }

  .album__item:first-child {
    width: 100%;
  }
}

@media (min-width: 1200px) {
  .album__item {
    width: 33%;
  }
}
